import { stripInvisibleChar } from "./string.js";
export function getHashtagTag(event, hashtag) {
    hashtag = stripInvisibleChar(hashtag.replace(/^#/, "").toLocaleLowerCase());
    return event.tags
        .filter((t) => t[0] === "t" && t[1])
        .find((t) => stripInvisibleChar(t[1].toLowerCase()) === hashtag);
}
